<template>
  <div class="ds-control ds-form-item ds-ueditor" :class="getCss">
    <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
      {{ label }}
      <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
    </label>
    <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
      <div ref="UEditor"></div>
      <div v-if="isDesign && readOnly" id="designReadOnly" :class="ueditorCss" v-text="msg"></div>
      <div v-if="!isDesign && readOnly" :class="ueditorCss">
        <div ref="uParse" v-html="value"></div>
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
      <div v-if="errors.length>0 && !readOnly" class="ds-error-text">{{ errorsMsg }}</div>
    </div>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";

export default dsf.component({
  name: "DsfUEditor",
  mixins: [formControl],
  ctrlCaption: "",
  props: {
    richTxtHeight: {
      type: Number,
      default: 400
    },
    placeholder: {
      type: String,
      default: ""
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("big-text-meta-data");
      }
    },
    toolBar: {
      type: Array,
      default(){
        return [
          'fullscreen',
          'undo',
          'redo',
          'bold',
          'italic',
          'underline',
          'removeformat',
          'formatmatch',
          'pasteplain',
          'forecolor',
          'backcolor',
          'insertorderedlist',
          'insertunorderedlist',
          'selectall',
          'cleardoc',
          'rowspacingtop',
          'rowspacingbottom',
          'lineheight',
          'customstyle',
          'paragraph',
          'fontfamily',
          'fontsize',
          'indent',
          'justifyleft',
          'justifycenter',
          'justifyright',
          'justifyjustify',
          'link',
          'unlink',
          'anchor',
          'imagenone',
          'imageleft',
          'imageright',
          'imagecenter',
          'simpleupload',
          'insertimage',
          'insertvideo',
          'inserttable',
          'autotypeset'
        ]
      }
    },
    showLabel:{
      type: Boolean,
      default: true
    },
    maximumWords: {
      type: Number,
      default: 10000
    }
  },
  computed: {
    ueditorCss() {
      return this.showLabel ? "have-label" : ""
    },
    msg() {
      const txt = "";
      const msg = this.visible ? txt + "" : txt + "";
      return msg
    }
  },
  watch: {
    "richTxtHeight"(val) {
      this.ue.setHeight(val)
    },
    //visible 
    "visible"(val) {
      val ? this.ue.setEnabled() : this.ue.setDisabled()
    },
    //readOnly 
    "readOnly"(val) {
      val ? this.ue.setHide() : this.ue.setShow();
    },
    "toolBar"() {
      this.renderUEditor()
    },
  },
  data() {
    return {
      ue: null,   //ueditor 
      ueId: null, //ueditor div Id
      uparseId: null,   //uParse  Id
    }
  },
  mounted() {
    const self = this;
    //
    if (!self.isDesign && self.readOnly) {
      const uparseId = dsf.uuid();
      self.$refs.uParse.id = uparseId;
      self.uparseId = uparseId;
      uparseContent.call(self);
      return
    }

    const id = dsf.uuid();
    self.$refs.UEditor.id = id;   //divid, UEditor
    self.ueId = id;

    const fileUrl = dsf.url.getWebPath("$/js/libs/UEditor/ueditor.config.js");
    const configUrl = dsf.url.getWebPath("$/js/libs/UEditor/ueditor.all.js");
    self.$http.importFiles([fileUrl, configUrl])
      .then(() => {
        self.renderUEditor();
      })
      .catch(err => {
        console.error("UEditor.js")
      })
  },
  beforeDestroy() {
    this.ue && this.ue.destroy();
    this.ue = null
  },
  methods: {
    renderUEditor() {
      const self = this;
      if (self.ue) self.ue.destroy();   //UEditor
      self.ue = UE.getEditor(self.ueId, {
        autoHeightEnabled: false,
        maximumWords: self.maximumWords,
        toolbars: [self.toolBar]
      });
      self.ue.ready(() => {
        //UEditor
        initUEditor.call(self);
      })
    }
  }
});

function uparseContent() {
  const fileUrl = dsf.url.getWebPath("$/js/libs/UEditor/ueditor.parse.js");
  this.$http.importFiles(fileUrl)
    .then(() => {
      uParse(this.uparseId, [{
        rootPath: "/static/js/libs/UEditor/",   //ueditor
        liiconpath: location.origin + "/static/js/libs/UEditor/themes/ueditor-list/",   //
        listDefaultPaddingLeft: '20'  //
      }])
    })
    .catch(err => {
      console.error("UEditor.parse.js")
    })
}

function initUEditor() {
  const self = this;
  $("#" + self.ueId).css("width", "100%");
  self.ue.setPlaceholder(self.placeholder);
  self.ue.setHeight(self.richTxtHeight);
  self.value && self.ue.setContent(self.value);   //UEditor

  self.ue.addListener("contentChange", function () {
    self.emitValueChange(self.ue.getContent());
    self.$emit("editor-change", {
      data: self.ue.getContent(),
      action: "contentChange"
    })
  });

  //inputbug
  self.ue.body.addEventListener("input", function () {
    self.emitValueChange(self.ue.getContent());
    self.$emit("editor-change", {
      data: self.ue.getContent(),
      action: "input"
    })
  });

  //
  self.ue.body.addEventListener("blur", function () {
    self.$nextTick(() => {
      self.emitFormValidate(self.ue.getContent());
    })
    self.$emit("editor-change", {
      data: self.ue.getContent(),
      action: "blur"
    })
  });

  if (self.isDesign) {
    self.visible ? self.ue.setEnabled() : self.ue.setDisabled();
    self.readOnly ? self.ue.setHide() : self.ue.setShow();
  } else {
    self.visible ? self.ue.setShow() : self.ue.setHide();
  }

}

</script>
