import "_platform/utils";
import "../dsf_config";
//
import "../assets/styles/pc/dsfStyle.scss";
//
import platformComponents from "_platform/pc";
// home
import Home from "_platform/pc/home/menu";
// 
import systemGlobalSetting from "_platform/mixins/systemGlobalSetting";

//
import "../metadata";
//api
import "../api";
//pc
import pcComponent from "./../pc/components";

// index
import HomeCtrl from "_platform/pc/home/ctrl";
// index
import HomeChart from "_platform/pc/home/chart";

import App from "./App.vue";
import store from "../store";
import router from "./router";

Vue.use(platformComponents);
Vue.use(Home).use(HomeCtrl).use(HomeChart);
Vue.use(pcComponent);

dsf.init.initPlatform(Vue);
dsf.init.globalMixin(Vue);

Vue.config.productionTip = false;

// 
let loginUrl = dsf.config.setting_public_pc_login_page;
let urlArr = loginUrl.split("#");
let path = urlArr.length > 1 ? urlArr[1] : null;
if (path) {
  let strs = path.split("?");
  if (strs.length < 2) {
    strs.push("from=${from}");
  } else if (strs.length == 2 && strs[1] == "") {
    strs[1] = "from=${from}";
  } else {
    strs[1] += "&from=${from}";
  }
  urlArr[1] = strs.join("?");
  loginUrl = urlArr.join("#");
}

router.beforeEach((to, from, next) => {
  // 
  if ((to.meta.needLogin || to.fullPath == "/") && !dsf.cookies.get("authorization_token")) {
    location.replace(loginUrl.replace("${from}", encodeURIComponent(location.href)));
    return;
  }
  //title
  document.title = to.meta.title || dsf.config.setting_public_logo_title || "";
  //
  next();
});

Vue.config.devtools = true;

new Vue({
  mixins: [systemGlobalSetting],
  store,
  router,
  created() {
    //windowresize
    dsf.resizeDetector();
    //IE
    if ("-ms-scroll-limit" in document.documentElement.style && "-ms-ime-align" in document.documentElement.style) {
      window.addEventListener(
        "hashchange",
        () => {
          var currentPath = window.location.hash.slice(1);
          console.log(router.replace);
          if (router.path !== currentPath) {
            router.replace(currentPath);
          }
        },
        false
      );
    }
  },
  render: (h) => h(App)
}).$mount("#app");
